import { j as json } from "../../../../../chunks/index.js";
import { P as Product } from "../../../../../chunks/product.js";
import { D as DailySales, a as DailySalesDetails } from "../../../../../chunks/daily_salesDetails.js";
async function GET({ request, params }) {
  try {
    const reporte = await DailySales.findByPk(params.id, {
      attributes: { exclude: ["createdAt", "updatedAt"] },
      include: [{
        model: DailySalesDetails,
        attributes: ["quantity", "net_sales", "av_price", "discount_amount"],
        include: [{
          model: Product,
          attributes: ["name"]
        }]
      }],
      order: [
        [DailySalesDetails, Product, "name", "ASC"]
      ]
    });
    return json(reporte);
  } catch (error) {
    console.error("no report by id: ", error);
    return { status: 500 };
  }
}
export {
  GET
};
